@import "../../App.scss";
section {
  position: relative;
  //   height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  //   border-top: 2px solid $secondColor;
  border-bottom: 2px solid $secondColor;
  margin: 2px 0;
  &.navSection__ad {
    p {
      max-width: 70%;
      font-size: 0.9rem;
      color: white;
      margin: 20px;
    }
    .signbtn__container {
      //   margin: 20px;
      margin-bottom: 20px;
    }
  }
  ul {
    width: 100%;
    height: 100%;
    padding: 7px;
    position: relative;
    list-style: none;
    li {
      width: 100%;
      // padding: 10px 30%;
      margin: 0 auto;
      padding: 10px 20px;
      cursor: pointer;
      display: flex;
      // justify-content: space-evenly;
      align-items: center;
      &:hover {
        i {
          color: white;
        }
      }
      i {
        color: lighten($color: $secondColor, $amount: 40);
        padding-right: 20px;
      }
      a {
        text-transform: capitalize;
        color: white;
        text-decoration: none;
      }
      &:hover {
        background-color: $secondColor;
      }
    }
  }
}
